export default{
	data(){
		return{
			url: 'https://a.app.qq.com/o/simple.jsp?pkgname=com.honey.jiaoyou',
			system:''
		}
	},
	onLoad() {
		this.determiningSystem();
	},
	methods:{
		determiningSystem(){
			var THAT = this;
			uni.getSystemInfo({
			    success: function (res) {
					if(res.system.includes("iOS")){
						THAT.system="iOS";
					}
					else{
						THAT.system="Android";
					}
			    }
			});
		},
		toDownloadUrl(){



			// app跳转
			// plus.runtime.openURL(this.url, function(res){
			// 	uni.showToast({
			// 		title:'跳转失败'
			// 	})
			// });  

			uni.navigateTo({
				url:'WebView?system='+this.system,
				success() {
					console.log("success");
				},
				fail(err) {
					console.log(err);
				}
			})

		},
		//右上角分享功能
		onShareAppMessage: function (res) {
			var that = this;
			return {
				title: '',
				path: '/pages/home/Home',
				imageUrl: "",
				success: function (res) {
					console.log(res);
				},
				fail: function (res) {
					console.log(res);
				}
			}
		},
	}
}
